import * as React from "react"

import Layout from "../components/layout"
import SeoBusiness from "../components/seoBusiness"
import NoWaterPoolImage from "../images/nowaterpool.jpg"

const NotFoundPage = () => {
 
  const divStyle = {
    background: 'linear-gradient(180deg,rgba(47,23,15,.3),rgba(47,23,15,.7) 61%), url('+ NoWaterPoolImage + ')',
    backgroundSize: 'cover'
  }

  return (
    <Layout>
      <SeoBusiness title="404: Not found" />
      <section className="page-section cta">
        <div className="container">
          <div className="row">
            <div className="col-xl-9 mx-auto">
              <div
                className="cta-inner bg-faded text-center rounded"
                style={divStyle}
              >
                <h2 className="section-heading mb-4">
                  <span className="section-heading-upper text-white">
                    This pool is empty
                  </span>
                  <span className="section-heading-lower text-white">
                    404: Page Not found
                  </span>
                </h2>
                <p className="text-white">
                Just hit the back arrow on your browser to go back...
                </p>
                <p className="text-white">
                Or
                </p>
                <p className="mb-0 text-white">
                  Choose a category from the navigation menu above.
                </p>
              </div>
            </div>
          </div>
        </div>
      </section>
    </Layout>
  )
}

export default NotFoundPage
